.headerContainer{
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 10;
    background-color: white;
    transition: 0.5s;
}
.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: 0;
    position: absolute;
    width: 100%;
    height: 60px;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    background-color: #fff;
    z-index: 10;
}
.headerLeft{
    display: flex;
    align-items: center;
}
.headerButton{
    margin-left: 2px;
    justify-content: center;
    /* border: 1px solid  rgb(37, 37, 37); */
    padding: 10px;
    border-radius: 7px;
    
}
.headerButton:hover{
    color: white;
    border: 1px solid  white;
    background-color: rgb(26, 185, 26);  
}

.headerButtonClicked{
    margin-left: 2px;
    justify-content: center;
    color: rgb(26, 185, 26);
    border: 1px solid  rgb(26, 185, 26);
    padding: 10px;
    border-radius: 7px;
}
.headerButtonClicked:hover{
    color: white;
    background-color: rgb(26, 185, 26);
}
.siteName{
    margin-left: 20px;
    font-size: x-large;
    font-weight: 800;
    color: rgb(26, 185, 26);
}
.headerRight{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.dropdown{
    display: none;
}

/* mobile responsive */
@media screen and (max-width:650px){
    .headerContainer{
        position: fixed;
        top: 0px;
        width: 100%;
        z-index: 10;
        background-color: white;
        transition: 0.5s;
    }
    .headerContainer.outOfView{
        top:-100px;
    }
}