.App {
  text-align: center;
}
a {
  /* to remove color change and underline on all anchor tags */
  text-decoration: none; 
  color: rgb(80, 79, 79);
}
.space{
  height: 100px;
}
.boldText{
  font-weight: bold;
}
.inactive{
  display: none;
}
/* while loading */
@keyframes animationName{
  0%{
    background-color: white;
  }
  25%{
    background-color: rgb(186, 221, 129);
  }
  50%{
    background-color: white;
  }
  75%{
    background-color: rgb(186, 221, 129);
  }
  100%{
    background-color: white;
  }
  
}

.greyBar{
  border-radius: 5px;
  width: 100px;
  padding: 15px 25px ;
  background-color: rgb(186, 221, 129);
  animation-name: animationName;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

