.chats{
    padding: 60px;
    padding-top: 40px;
    height: 80vh;
}
.chats .container{
    margin: 40px;
    border: 0.5px solid grey;
    height: 80vh;
    border-radius: 20px;
    overflow: hidden;
    position: relative;

}
.chats .container:hover{
    border: 0.5px solid rgb(26, 185, 26);
}
.chats .container .top{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.chats .container .container-title{
    margin: 30px;
    text-align: left;
    font-size: 25px;
    color: rgb(26, 185, 26);
    font-weight: bolder;
}
.chats .container .chats-search {
    display: flex;
    flex-direction: row;

    margin: 20px;
    color: rgb(87, 88, 87);
    background-color: rgb(248, 248, 245);
    border-radius: 5px;
    width: 50vw;
    height: 40px;
}
.chats .container .chats-search  input{
    outline: none;
    width: 44vw;
    border: 1px solid rgb(26, 185, 26);
    border-radius: 5px 0px 0px 5px;
}
.chats .container .chats-search .search-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 185, 26);
    color: white;
    width: 6vw;  
    border-radius: 0px 5px 5px 0px;
     
}
.chats .container .chats-list{
    display: flex;
    flex-direction: column;
    height: 75vh;
    margin: 5px;
    overflow-y: auto;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.chats .container .chats-list::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.chats .container .chats-list{
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.chats .container .discover-chat-btn{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 5px;
    bottom: 110px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    color: white;
    background-color: rgb(26, 185, 26);
    box-shadow: 0px 0px 10px grey;
}

/* mobile responsive css */
@media screen and (max-width:650px) {
    .chats{
        padding: 0px;
        padding-top: 10px;
    }
    .chats .container{
        width: 80%;
        margin: auto;
        margin-right: 0;
        margin-left: 50px;
        border: none;
        height: 100vh;
        border: none;
        border-radius: 20px;
        overflow: hidden;
    
    }
    .chats .container:hover{
        border: none;
    }
    .chats .container .top{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
    }
    .chats .container .container-title{
        text-align: left;
        font-size: 25px;
        color: rgb(26, 185, 26);
        font-weight: bolder;
    }
    .chats .container .chats-search {
        display: flex;
        flex-direction: row;
    
        margin: 10px;
        color: rgb(87, 88, 87);
        background-color: rgb(248, 248, 245);
        border-radius: 5px;
        width: 95%;
        height: 40px;
    }
    .chats .container .chats-search  input{
        outline: none;
        width: 80%;
        border: 1px solid rgb(26, 185, 26);
        border-radius: 5px 0px 0px 5px;
    }
    .chats .container .chats-search .search-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgb(26, 185, 26);
        color: white;
        width: 95px;  
        border-radius: 0px 5px 5px 0px;
         
    }
    .chats .container .chats-list{
        display: flex;
        flex-direction: column;
        height: 80vh;
        margin: 5px;
        overflow-y: auto;

    }
}