.portfolio{
    padding-top: 80px; /*avoiding the fixed header*/
    width: auto;
    max-width: 450px;/*360px;*/
    margin: auto;
    padding-bottom: 100px;
}
.portfolio h2{
    color: rgb(26, 185, 26);
}
.portfolio .portfolioUpperSection{
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    /* section border implementation */
    padding: 15px;
    border: 1px solid grey;
    border-radius: 5px;
    padding-bottom: 35px;
}
.portfolio .portfolioUpperSection:hover{
    border: 1px solid rgb(26, 185, 26);
}

.portfolio .portfolioUpperSection .columns{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.portfolio .portfolioUpperSection .columns .column{
    
}
.portfolio .portfolioUpperSection  .row{
    display: flex;
    flex-direction: row;
    margin-top: 25px;

}

.portfolio .boldText{
    margin-left: 5px;
}

.portfolioLowerSection{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    margin-bottom: 20px;
    /* section border implementation */
    padding: 15px;
    border: 1px solid grey;
    border-radius: 5px;
    padding-bottom: 15px;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    
}

.portfolio .portfolioLowerSection:hover{
    border: 1px solid rgb(26, 185, 26);
}

.portfolio .portfolioLowerSection .portfolioLowerSectionHeaders{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    /* border */
    padding: 5px;

}
.portfolio .portfolioLowerSection .portfolioLowerSectionHeader{
    font-weight: bold;
    font-size: 17px;
    width: 100px;
}

/* making projects scrollable */
.portfolio .portfolioLowerSection .portfolioProjects{
    overflow-y: auto; /*creating scrollable overflow*/
    height: 400px;
    scrollbar-width: none; /*scrollbar invisible*/ 
    overscroll-behavior-inline: contain;
}
/* making scroll bar invisible to more browsers */
.portfolio .portfolioLowerSection .portfolioProjects::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}