.portfolioProject{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
    /* border */
    padding: 5px;
    border: 1px solid grey;
    border-radius: 5px;
    
}
.portfolioProject:hover{
    border: 1px solid rgb(26, 185, 26);
}
.portfolioProject .column{
    width: 100px;
    height: 45px;
    /* just to align text to the center */
    display: flex;
    justify-content: center;
    align-items: center;
}
.portfolioProject .colouredCol{
    background-color: rgb(26, 185, 26);
    color: white;
    border-radius: 5px;
    font-weight: 600;
}