.news{

}
.news .title-container{
    margin-left: 150px;
    padding-top: 80px;
    text-align: left;
    font-size: 25px;
    color: rgb(26, 185, 26);
    font-weight: bolder;
}

.news .postContainer{
    padding: 100px;
}
@media screen and (max-width:650px) {
    .news{
        padding: 20px;
        padding-left: 40px;

    }
    .news .title-container{
        margin: 20px;
        padding-top: 0px;
        text-align: left;
        font-size: 25px;
        color: rgb(26, 185, 26);
        font-weight: bolder;
    }
    
    .news .postContainer{
        padding-top: 80px;
        padding: 17px;
        padding-left: 5px;
    }
}