.newsDetail{
    padding-top: 90px;
    width: 50%;
    margin: auto;
}
.newsDetail .newsPostImg{
    padding: 10px;
    border: 0.5px solid grey;
    border-radius: 5px;
    margin-bottom: 80px;
    
}
.newsDetail .newsPostImg:hover{
    border: 0.5px solid rgb(26, 185, 26);
}
.newsDetail .newsPostImg img{
    max-width: 100%;
    max-height: 100%;
    object-fit: fill;
    
}
.newsDetail .newsPostDetail{
    margin-bottom: 80px;
    border: 0.5px solid grey;
    border-radius: 5px;
    padding: 20px;
}
.newsDetail .newsPostDetail:hover{
    border: 0.5px solid rgb(26, 185, 26);
}
.newsDetail .comments{
    border: 0.5px solid grey;
    border-radius: 5px;
    margin-bottom: 80px;
    padding: 30px;  
}
.newsDetail .comments:hover{
    border: 0.5px solid rgb(26, 185, 26);
}
.newsDetail .title-section{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.newsDetail .title-section .title{
    margin: 30px;
    text-align: left;
    font-size: 25px;
    color: rgb(26, 185, 26);
    font-weight: bolder;
}

@media screen and (max-width:650px) {
    .newsDetail{
        padding-top: 90px;
        width: 70%;
        margin: auto;
    }
}