
.filtersContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    margin-left: 25% ;
    height: 70px;
    transition: 0.5s;
    position: fixed;
    top: 39px;
    z-index: 1;
    background-color: white;
    border: 1px solid rgb(26, 185, 26);
    border-radius: 5px;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    overflow-x: auto;
    scrollbar-width: none; 
    overscroll-behavior-inline: contain;
    padding: 10px;
}
.filtersContainer::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}
.filterList{
    display: flex;
    flex-direction: row;
    align-items: center;
    list-style-type: none;
}
/* [feature state]: it is failing to snap , i think it's cuz i used li not div elements*/
.snap-inline{
    scroll-snap-type: inline mandatory;
}
.snap-inline > li{
    scroll-snap-align: start;
}
.filterListElement{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: rgb(26, 185, 26);
    border-radius: 5px;
    height: 30px;
    width: 100px;
    margin: 10px;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
}

/* adding responsiveness through media quiries */
@media only screen and (max-width:800px) {
    .filtersContainer{
        width: 100%;
        margin-left: 1px;
    }
}