.msgThread{
    width: 300px;
    background: linear-gradient( to bottom, rgb(199, 197, 197), rgb(131, 130, 130));
    margin: 10px;
    border-radius: 14px;
    padding: 10px;
    font-size: 16px;
    color: white;
}
.msgThread.me{
    background: linear-gradient( to bottom, rgb(59, 235, 59), rgb(26, 185, 26));
    color: white;
    align-self: flex-end;

}
.msgThread .msgUser{
    text-align: left;
    margin: 10px;
    margin-top: 5px;
    margin-bottom: 20px;
    font-weight: bolder;
}
.msgThread .msg{
    font-weight: bold;
}
.msgThread .meta{
    text-align: right;
    font-size: 14px;
    margin-top: 10px;
    margin-right: 10px;
}

@media screen and (max-width:650px) {
    .msgThread{
        width: 150px;
        background: linear-gradient( to bottom, rgb(199, 197, 197), rgb(131, 130, 130));
        margin: 10px;
        margin-left: 5px;
        border-radius: 14px;
        padding: 10px;
        font-size: 16px;
        color: white;
    }
    .msgThread.me{
        background: linear-gradient( to bottom, rgb(59, 235, 59), rgb(26, 185, 26));
        color: white;
        align-self: flex-end;
    
    }
    .msgThread .msgUser{
        text-align: left;
        margin: 10px;
        margin-top: 5px;
        margin-bottom: 20px;
        font-weight: bolder;
    }
    .msgThread .msg{
        font-weight: bold;
    }
    .msgThread .meta{
        text-align: right;
        font-size: 14px;
        margin-top: 10px;
        margin-right: 10px;
    }
}