
.sidebar{
    width: 280px;
    min-height: 100vh;
    box-shadow: 0px 4px 8px rgb(0 0 0 / 16%);
    background-color: #fff;
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 2;
    transition: 0.5s;
    border-radius: 15px;
}
.sidebar.active{
    left: 0;

}
.sidebarBody{
    margin-top: 100px;
    padding: 15px;
    max-height: calc(100vh - 67px);
    overflow-x: hidden;
}
.sidebarOption{
    /* centering text */
    display: flex;
    justify-content: center;
    align-items: center;

    /* sidebar pallet */
    background-color: white;
    border: 1px solid rgb(26, 185, 26);
    border-radius: 10px;
    height: 40px;
    margin: 10px;

    /* sidebar font */
    color: rgb(26, 185, 26);
    font-size: 19px;
    font-weight: 550;
  
}
.logout{
    border: 1px solid rgb(182, 3, 3);
    color: rgb(182, 3, 3);
} 
.sidebarOption:hover{
    background-color: rgb(26, 185, 26); 
    color: white;
}
.logout:hover{
    background-color: red; 
    color: white;
}
.copyright{
    margin-left: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    bottom: 10px;
    color: grey;
}
.copyright .colouredText{
    margin-left: 5px;
    color: rgb(2, 145, 2);
}

/* adding responsiveness through media quiries */
@media only screen and (max-width:1000px) {
    .copyright{
        bottom: 100px;

    }
}