.home{
    width: auto;
    margin: auto;
}

.home h2{
    text-align: left;
    margin-left: 20px;
    color:rgb(26, 185, 26);
}
.banner{
    display: flex;
    justify-content: center;
    align-items: center; /* So as to center the inner text */
    width: 100%;
    height: 500px;
    background-image: url('https://cdn.pixabay.com/photo/2014/11/30/07/16/watermelon-551235_960_720.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0px 4px 4px 0px #00000040,inset 0 0 0 1000px rgba(0,0,0,.5) /* blackened overlay */;
    overflow: hidden;
}
.banner_text{
    color: rgb(247, 247, 247);
    font-size: Xx-large;
    font-weight: 900;
}
.siteSummary{
    margin: 50px;
    font-size: 22px;
    font-weight: 600;
    color: rgb(90, 90, 88);
}
/* type writer effect */
.typewriter h3 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid rgb(26, 185, 26); /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  /* The typing effect */
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgb(26, 185, 26); }
  }


.sectionContainer{
    margin-top: 50px;
}
.sectionTop{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-right: 25px;
    
}
.moreItemsButton{
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    color: rgb(26, 185, 26);
    background-color: #fff;
    padding: 5px;
    border-radius: 10px;
    border: 1px solid rgb(26, 185, 26);
}
.moreItemsButton:hover{
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    background-color: rgb(26, 185, 26);
    color: #fff;
    padding: 5px;
    border-radius: 10px;

}
.item_list{
    width: 97%;
    margin: auto;
}


/* adding responsiveness through media quiries */
@media only screen and (max-width:700px) {
    .banner_text{
        font-size: 14px;    
    }
    .item_list{
        width: 80%;
        margin: auto;
    }
}