        box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    }
    .msgPanel .top .topPanelRight, .msgPanel .top .topPanelLeft{
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
    }
    .msgPanel .top .topPanelRight:hover, .msgPanel .top .topPanelLeft:hover {
        background-color: white;
        color:rgb(21, 77, 21);
    }
    .msgPanel .top .topPanelMiddle{
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 20px;
        font-weight: bolder;
    }
    .msgPanel .top img{
        width: 50px;
        border-radius: 50%;
    }
    .msgPanel .top .topPanelMiddle .info{
        margin-left: 10px;
    }
    .msgPanel .middle{
        height: 100%;
        overflow-y: auto;
    }
    .msgPanel .middle .msg-container{
        display: flex;
        flex-direction: column;
        margin: 5px;
        margin-right: 0px;
        margin-top: 20px;
        margin-bottom: 70px;
    }
    /* Hide scrollbar for Chrome, Safari and Opera */
    .msgPanel .middle::-webkit-scrollbar {
        display: none;
    }
    
    /* Hide scrollbar for IE, Edge and Firefox */
    .msgPanel .middle{
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
    }
    
    .msgPanel .bottom{
        position: fixed;
        left: 50%;
        transform: translate(-50%);
        bottom: 6%;
        margin: auto;
        display: flex;
        flex-direction: row;
    
    }
    .msgPanel .bottom .panel{
        display: flex;
        flex-direction: row;
        align-items: center;
        border: 0.5px solid grey;
        border-radius: 10px;
        padding: 8px;
        color: rgb(83, 83, 83);
        background-color: white;
    }
    .msgPanel .bottom .panel .emoji-toggle{
        width: 40px;
    }
    .msgPanel .bottom .panel .gif-sticker-toggle{
        width: 40px;
    }
    .msgPanel .bottom .panel input{
        border: none;
        outline: none;
        width: 100px;
    
    }
    .msgPanel .bottom .panel .attach-toggle{
        width: 40px;
    }
    .msgPanel .bottom .panel .emoji-toggle:hover,.msgPanel .bottom .panel .gif-sticker-toggle:hover,.msgPanel .bottom .panel .attach-toggle:hover {
        color:rgb(26, 185, 26);
    }
    .msgPanel .bottom .send-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgb(15, 116, 15);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-left: 5px;
        color: white;
    }
    .msgPanel .bottom .send-btn:hover{
        background-color: white;
        border: 0.5px solid rgb(26, 185, 26);
        border-radius: 50%;
        color:rgb(26, 185, 26);
    } 
}