.community-sidebar{
    position: fixed;
    padding-top: 70px;
    display: flex;
    flex-direction: column;
    background-color: white;
    width: 65px;
    height: 100vh;
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);

    left: -200px;
    transition: 0.5s;
    border-radius: 0px 20px 20px 0px;
}
.community-sidebar.active{
    left: 0px;
}
.community-sidebar .community-sidebar-btn{
    display: flex;
    flex-direction: column;
    height: 50px;
    margin-top: 15px;
    padding: 5px;
    font-size: 11px;
    /* border: 1px solid rgb(26, 185, 26); */
    border-radius: 5px;
}
.community-sidebar .community-sidebar-btn:hover{
    background-color: rgb(26, 185, 26);
    color: white;
}
.community-sidebar .community-sidebar-btn .btn-icon{
    margin: auto;
}
@media screen and (max-width:650px) {
    .community-sidebar{
        position: fixed;
        padding-top: 0px;
        display: flex;
        flex-direction: column;
        background-color: white;
        width: 45px;
        height: 100vh;
        box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    }
}