.postFooter{
    position: relative;
    background-color: white;
    color: rgb(46, 45, 42);
    text-align: center;
    height: 120px;
}
.footerTitle{
    color: rgb(46, 45, 42);
}
.footerDescription{
    /* to truncate the text */
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    margin: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    height:70px;
}