.newsPost{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border: 0.5px solid grey;
    width: 75%;
    margin: auto;
    margin-bottom: 40px;
    padding: 10px;
    border-radius: 20px;
    
}
.newsPost:hover{
    background-color: rgb(90, 190, 90);
    color: white;
}
.newsPost .postLeft{

}
.newsPost .postLeft .postTitle{
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
}
.newsPost .postLeft .postAuthor{
    display: flex;
    flex-direction: row;
    color: grey
}
.newsPost .postRight img{
    width: 400px;
    border-radius: 20px;
}



/* mobile responsiveness */
@media screen and (max-width:650px) {
    .newsPost{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        border: 0.5px solid grey;
        width: auto;
        margin: 10px;
        margin-bottom: 40px;
        padding: 10px;
        padding-right: 0px;
        border-radius: 20px;
        height: 150px;
        width: 95%;
    }
    .newsPost:hover{
        background-color: rgb(90, 190, 90);
        color: white;
    }
    .newsPost .postLeft{

    }
    .newsPost .postLeft .postTitle{
        text-align: left;
        font-size: 20px;
        font-weight: bolder;
        margin-bottom: 10px;
    }
    .newsPost .postLeft .postAuthor{
        display: flex;
        flex-direction: row;
        color: grey
    }
    .newsPost .postRight img{
        width: 140px;
        height: 155px;
        border-radius: 20px;
        object-fit: cover;
        margin: 5px;
    }
    
}