.chatTile{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 500px;
    margin: auto;
    margin-bottom: 30px;
    border: 0.1px solid rgb(235, 234, 234);
    padding: 20px;
    border-radius:30px;
    overflow: hidden;
}

.chatTile:hover{
    background-color: rgb(19, 204, 19);
    color: white;
}

.chatTile img{
    width: 50px;
    border-radius: 50%;
}

.chatTile .middle{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.chatTile .middle .chatName{
    font-size: 18px;
    font-weight: bolder;
}
.chatTile .middle .lastMsgContainer{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 300px;
    color: rgb(71, 71, 71);

}
.chatTile .right{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.chatTile .right .time{

}
.chatTile .right .unreadMsgCount{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    background-color: rgb(19, 204, 19);
    color: white;
    height: 25px;
    width: 25px;
    border-radius: 50%;
}

/* responsivenss css */
@media screen and (max-width: 650px) {
    .chatTile{
        width: 85%;
        height: 40px;
    }

    
    .chatTile img{
        width: 45px;
        border-radius: 50%;
    }
    
    .chatTile .middle{
        margin-left: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

    }
    .chatTile .middle .chatName{
        font-size: 18px;
        font-weight: bolder;
        align-self: flex-start;
    }
    .chatTile .middle .lastMsgContainer{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 150px;
        color: rgb(71, 71, 71);
        overflow: hidden;
    
    }
    .chatTile .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-self: flex-start;
        align-items: center;
    }
    .chatTile .right .time{
    
    }
    .chatTile .right .unreadMsgCount{
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bolder;
        background-color: rgb(19, 204, 19);
        color: white;
        height: 25px;
        width: 25px;
        border-radius: 50%;
    }

}