.register{
    margin-top: 80px;
  }
.register h2 {
    color: rgb(26, 185, 26);
  }
  
  .registerInputForm{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .formField{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .registerInput{
    height: 30px;
    border: 1px solid rgb(26, 185, 26);
    outline: none;
    margin-top: 10px;
    margin-bottom: 15px;
    margin-right: 5px;
    margin-left: 15px;
    color: rgb(87, 88, 87);
    background-color: rgb(248, 248, 245);
    border-radius: 5px;
    width: 45vh;
  }
  .coloredText{
    color: rgb(17, 117, 17);
  }
  .register button{
    height: 40px;
    width: 100px;
    border: none;
    outline: none;
    margin-top: 10px;
    margin-bottom: 15px;
    margin-right: 25px;
    background-color: rgb(26, 185, 26);
    color: white;
    border-radius: 5px;
    font-size: 15px;
    font-weight: bold;
  }
  .registerError{
    color: rgb(207, 0, 0);
  }

  /* adding responsiveness through media quiries */
@media only screen and (max-width:700px) {
    .register{
        margin-top: 100px;
        margin-left: 5px;
        margin-right: 10px;
      }
}