.search{
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: auto;
    justify-content: center;
    height: 80px;
    transition: 0.5s;
    position: fixed;
    top: -100%;
    z-index: 1;
    color: rgb(26, 185, 26);
    background-color: white;
}
.search.active{
    top: 60px;
}
.searchInput{
    border: 1px solid rgb(26, 185, 26);
    outline: none;
    margin-top: 20px;
    margin-bottom: 15px;
    margin-right: 5px;
    margin-left: 15px;
    color: rgb(87, 88, 87);
    background-color: rgb(248, 248, 245);
    border-radius: 5px;
    width: 45vh;
}
.searchButton{
    border: none;
    outline: none;
    margin-top: 20px;
    margin-bottom: 15px;
    margin-right: 25px;
    background-color: rgb(26, 185, 26);
    color: white;
    border-radius: 5px;
    font-size: 15px;
    font-weight: bold;
}