.footer-activate-btn{
    transition: 0.5s;
    position: fixed;
    bottom: -10px;
    left: 25%;
    width: 50%;
    height: 5px;
    background-color: white;
    color: rgb(65, 65, 64);
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgba(12, 12, 12, 0.3);
    justify-content: space-evenly;
    z-index: 10;
    border: 2px solid rgb(26, 185, 26);
    background-color: rgb(19, 204, 19);

}
.footer-activate-btn.active{ /* active makes either the btn or foooter appear by setting it's bottom on screen */
    bottom: 10px; 
}

.footer{
    transition: 0.5s;
    position: fixed;
    bottom: -100px;
    left: 25%;
    width: 50%;
    height: 70px;
    background-color: white;
    color: rgb(65, 65, 64);
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgba(12, 12, 12, 0.3);
    justify-content: space-evenly;
    z-index: 10;
    border: 2px solid rgb(26, 185, 26);
}
.footer.active{ /* active makes either the btn or foooter appear by setting it's bottom on screen */
    bottom: 10px; 
}


.footerOption{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin: 5px;
    border-radius: 5px;
    padding: 10px;
    transition: 0.4s;
    
}
.footerOption:hover{
    background-color: rgb(26, 185, 26);
    color: white;
    padding: 10px;
}
.footer .active{
    background-color: rgb(26, 185, 26);
    color: white;
    padding: 10px;
}

/* adding responsiveness through media quiries */
@media only screen and (max-width:800px) {
    .footer{
        position: fixed;
        left: 5%;
        width: 90%;
    }
    .footer.active{
        bottom: 20px;
    }
}