
/* for preloader */

.loader-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    display: grid;
    place-items: center;
    z-index: 5;
  }
  
  .loader {
    background-color: rgb(26, 185, 26);
    width: 80px;
    height: 80px;
    animation: loaderAnimation 5s linear infinite;
  }
  
  @keyframes loaderAnimation {
    0% {
        border-radius: 0px;
        transform: rotate(0deg);
    }
    25% {
        border-radius: 50%;
        background-color: rgb(64, 235, 64);
    }
    50% {
        border-radius: 0px;
    }
    75% {
        border-radius: 50%;
        background-color: rgb(64, 235, 64);
    }
    100% {
      transform: rotate(360deg);
      border-radius: 0px;
    }
  }