.profile{
    padding-top: 100px;
    width: auto;
    margin: auto;
    padding-bottom: 100px;
}
.profile .profileImg img{
    width: 220px;
    height: 220px;
    border-radius: 50%;
    object-fit: cover;
    box-shadow: 0 4px 10px 0 rgba(122, 120, 120, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
}
.profile .profileForm {
    display: flex;
    flex-direction: column;
    align-items: center; 
}
.profile .profileForm h2{
    color:rgb(26, 185, 26);
}
.profile .formField {
    display: flex;
    font-display: row;
    justify-content: space-between;
    width: 98%;
    max-width: 400px;
    margin-top: 20px;   
}
.profile .formField label{
    font-size: 16px;
    font-weight: bolder;
    color: rgb(58, 57, 57);
}
.profile .formField input{
    margin-left: 10px;
    height: 30px;
    width: 250px;
    border: 1px solid grey;
    border-radius: 5px;
    box-shadow: 0 4px 10px 0 rgba(122, 120, 120, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
    background-color: rgb(244, 250, 244);

}
.profile .formField input:hover{
    border: 1px solid rgb(26, 185, 26);
}
.profile .formField input:focus{
    outline: none !important;
    border: 1px solid rgb(26, 185, 26);
    box-shadow: 0 0 10px rgb(169, 240, 169);
}
.profile button{
    height: 40px;
    width: 100px;
    border: none;
    outline: none;
    margin: auto;
    background-color: rgb(26, 185, 26);
    color: white;
    border-radius: 5px;
    font-size: 15px;
    font-weight: bold;
}