/* information section */
.detail{
    display: flex;
    flex-direction: row;
    padding-top: 90px;
    padding-bottom: 100px;
}
.mainSection {
    margin-right: 290px;
}
.mainSection img {
    width: 85%;
    padding: 5px;
    border: 1px solid grey;
    border-radius: 5px;
}
.mainSection img:hover{
    border: 1px solid rgb(26, 185, 26);
}
.mainSection .postDetail h3{
    text-align: center;
    color: rgb(4, 179, 4);
}
.mainSection .postDetail .postInfo{
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: rgb(95, 96, 97);
    margin-bottom: 10px;

}
.mainSection .postDate{
    display: flex;
}
.mainSection .postViews{
    display: flex;
    margin-left: 20px;
}

.mainSection .postDetail{
    width: 85%;
    padding: 5px;
    font-size: 20px;
    border: 1px solid grey;
    border-radius: 5px;
    margin: auto;
    margin-top: 30px;
}
.mainSection .postDetail:hover{
    border: 1px solid rgb(26, 185, 26);
}


/* Investing section */

.rightSideSection {
    background-color: white(253, 253, 253);
    box-shadow: 0 4px 5px 0 rgba(122, 120, 120, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    height: 350px;
    position: fixed;
    right: 30px;
    border-radius: 5px;
}
.rightSideSection .rightSideContent{
    position: sticky;
    width: 300px;
}
.rightSideSection .rightSideContent h3{
    color: rgb(26, 185, 26);  
}
.rightSideSection .rightSideContent .infoRow{
    margin-top: 5px;
    display: flex;
    flex-direction: row;
}

.infoRow .rowText{
    margin-left: 6px;
}
.infoRow .rowValue{
    margin-left: 6px;
}
.infoRow .infoRowButtonSeries{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: auto;
    margin-top: 20px;
}

.infoRow .infoRowButtonSeries .button{
    border: 1px solid grey;
    border-radius: 5px;
    padding: 10px;
    max-width: 100px;
    display: flex;
    align-items: center;
}
.infoRow .infoRowButtonSeries .button:hover{
    border: 1px solid rgb(26, 185, 26);
    color: rgb(26, 185, 26);

}
.infoRow .infoRowButtonSeries .button input{
    border: none;
    outline: none;
    width: 100%;
}

.infoRow .investButton{
    margin: auto;
    margin-top: 20px;
    color: white;
    font-size: 23px;
    font-weight: bold;
    border-radius: 5px;
    padding: 10px 80px 10px 80px;
    background: linear-gradient(to bottom, rgb(186, 221, 129), rgb(26, 185, 26));
}
.infoRow .investButton:hover{
    border: 2px solid rgb(23, 129, 23);
    background: rgb(23, 129, 23);
}

/* adding responsiveness through media quiries */
@media only screen and (max-width:1000px) {
    .detail{
        display: flex;
        flex-direction: column;
        margin-top: 80px;
    }
    .mainSection {
        margin-right: 0px;
    }
    .mainSection img{
        width: 80%;
    }
    .mainSection .postDetail{
        width: 80%;
    }
    .rightSideSection {
        width: 80%;
        margin: auto;
        padding: 5px;
        margin-top: 20px;
        position: relative;
        right: 0px;
        background-color: white;
    }

}